import React from 'react'
import './style.css';
import StarRoundedIcon from '@mui/icons-material/StarRounded';
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Pagination, Navigation } from "swiper";

function Card({ card }) {
    return (
        <div className='card-box'>
            <Swiper
                slidesPerView={1}
                spaceBetween={15}
                loop={true}
                mousewheel={true}
                cssMode={true}
                pagination
                modules={[Pagination, Navigation]}
                className='swiper-container'
            >
                {card.imgSrc.map((src, i) => (
                    <SwiperSlide key={i}>
                        <img src={src} alt='' className='card-img' />
                    </SwiperSlide>
                ))}
            </Swiper>
            <div className='card-img-title-rating-box'>
                <div className='card-info-flex'>
                    <h3 className='card-title'>{card.title}</h3>
                    <div className='card-rating'>
                        <StarRoundedIcon style={{ fontSize: '1.1rem' }} />
                        <p style={{ fontWeight: 400 }}>{card.rating}</p>
                    </div>
                </div>
                <p style={{ margin: 0, color: 'var(--font-grey)' }}>{card.description}</p>
                <p style={{ margin: 0, color: 'var(--font-grey)' }}>{card.date}</p>
            </div>
            <p style={{ fontWeight: 600, marginTop: '0.2rem' }}>₹{card.price} <span style={{ fontWeight: 400 }}>night</span></p>
        </div>
    )
}

export default Card;